h1, .h1 {
  background: url(https://raw.githubusercontent.com/hd1313169/aobaBadminton/main/app/assets/images/aoba_logo_48x3.svg) no-repeat left center;
  width: 128px;
  height: 40px;
  text-indent: 101%;
  white-space: nowrap;
  overflow: hidden;
}

.navbar-decoration {
  display: flex;
  width: 100%;
  height: 4px;
}

.navbar-decoration-left {
  width: 50%;
  height: 4px;
  background: #56BBB5;
}

.navbar-decoration-right {
  width: 50%;
  height: 4px;
  background: #FBD64A;
}

.navbar-desktop {
  display: none;
}

@media (min-width: 768px) {
  .navbar-desktop {
    display: block;
  }
}

.nav-item-btn:hover {
  color: #56BBB5 !important;
}

.nav-item-btn-logOut:hover {
  color: #FBB84A !important;
}

.footer {
  border-top: 1px solid #D9D9D9;
}

.hero {
  width: 100%;
}

@media (min-width: 768px) {
  .coachCard-swiper {
    display: block;
  }
}

.swiper-slide {
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#customBtn {
  display: inline-block;
  background: white;
  border-radius: 32px;
  border: 1px solid #d9d9d9;
  white-space: nowrap;
}

#customBtn:hover {
  cursor: pointer;
}

span.label {
  font-family: serif;
  font-weight: normal;
}

.icon {
  display: inline-block;
  vertical-align: middle;
  width: 42px;
}

span.buttonText {
  display: inline-block;
  vertical-align: middle;
